import React from "react";

import ribbon from './../assets/img/first-place-ribbon.png';
import { useNavigate } from "react-router-dom";

import AssesmentAlert from "./AssesmentAlert";

function Login(props) {
  const navigate = useNavigate();
  const [openDialog, setOpenDialog] = React.useState(false);

  function handleClickNavigate(path) {
    navigate(path);
  }

  const handleConfirmLogout = () => {
    props.revokeToken();
  }

  return (
    <div className={`indie-font h-screen w-full grid content-center`}>
      <div className="relative h-[667px] bg-[#2E683E] w-[375px] mx-auto rounded-xl shadow-md overflow-hidden border-4 border-slate-50">
        <div className="mx-auto w-full h-[100px] text-center align-middle flex flex-row">
          <div className="flex justify-center items-center h-full w-full mx-auto">
            <img src={ribbon}
              width={48}
              height={48}
              alt="Logo Sinala Care"
              className="mr-5"
            />
            <div>
              <h1 className="text-3xl font-bold text-white">Sinala Care</h1>
              <p className={`text-md font-thin text-slate-200`}></p>
            </div>
          </div>
        </div>
        <div className="absolute bottom-0 left-0 bg-slate-200 h-[567px] w-full rounded-t-xl">
          <div className="relative h-full rounded-xl">
            <div className="w-full h-full text-center">
              <button className="w-[80%] mt-10 bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 border-b-4 border-blue-700 hover:border-blue-500 rounded"
                type="button"
                onClick={() => handleClickNavigate('/studi-kasus')}>
                Studi Kasus
              </button>
              <button className="w-[80%] mt-6 bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 border-b-4 border-blue-700 hover:border-blue-500 rounded"
                type="button"
                onClick={() => handleClickNavigate('/profil')}>
                Profil
              </button>
              <button className="w-[80%] mt-6 bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 border-b-4 border-blue-700 hover:border-blue-500 rounded"
                type="button"
                onClick={() => { setOpenDialog(true) }}>
                Logout
              </button>
            </div>
            <div className="absolute bottom-2 w-full text-center">
              <p className={`text-md font-thin text-slate-500`}>Sinala Care v-0.0.1</p>
              <p className={`text-sm font-thin text-slate-500`}>&copy; 2023 - Universitas Hang Tuah Surabaya</p>
            </div>

            <AssesmentAlert
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              confirmCallback={handleConfirmLogout}
              message={'Apakah anda yakin untuk keluar dari aplikasi ini ?'}
            />
          </div>
        </div>
      </div>
    </div>);
}

export default Login;